.row_poster{
    object-fit: contain;
    width: 100%;
    max-height: 150px;
    transition: transform 450ms;
    margin-right: 10px;
}
.row_posters{
    display:flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_posters::-webkit-scrollbar{
    display: none;
}

.row_poster:hover{
    transform: scale(1.08);
    opacity: 1;
}

.row_posterLarge{
    max-height: 300px;
}

.row_posterLarge:hover{
    transform: scale(1.09);
    opacity: 1;
   
}
.row{
    margin-left: 20px;
    color: white;
}