.nav{
    
    position:fixed;
    top:0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 20px;
    height: 30px;
    z-index: 1;

    transition-timing-function: ease-in;
    transition: all 1s;

}


.nav__black{
    background-color: #111;
}

.nav__logo{
    position: fixed;
    left: 20px;
    width:90px;
    object-fit: contain;
}

.nav__avatar{
    position: fixed;
    right: 20px;
    width:50px;
    object-fit: contain;
}